<h1 id="writing-exam-answers" tabindex="-1"><a class="header-anchor" href="#writing-exam-answers" aria-hidden="true">#</a> Writing exam answers</h1>
<p>Which spelling of the name is correct?</p>
<ul>
<li>peter</li>
<li>Peter</li>
</ul>
<p>Justify your answer.</p>
<p>Peter
Worst answer, no justification provided. 0 points.</p>
<p>Peter, because it starts with a capital P
Better answer, but not a valid justification (could just as well be <em>peter, because it start with a lowercase p</em>). 0 points.</p>
<p>Peter, because it consists of 5 characters.
Better answer, but the (implicit) grammar rule <em>Words consisting of 5 characters should start with uppercase character</em> does not exist. 0 points.</p>
<p>Peter, because there's a grammar rule stating that characters consisting of 5 characters should start with an uppercase character.
Better answer, because the grammar rule is explicitly stated. Unfortunately, that grammar rule doesn't exist. 0 points.</p>
<p>Peter, because it's a name.
Better answer, because the justification is correct that Peter is a name, but still doesn't say why it should start with a capital P (<em>peter</em> is a name too, why is that wrong?), 0 or 0.5 points.</p>
<p>Peter, because it's a name, and the English grammar states all names should start with a capital letter.
Best answer, because <em>Peter</em> is identified as a name, and then justify that it should start with a capital letter because there's a grammatical rule in the English language that says all names should start with a capital letter. 1 point</p>
<p>(to be really scientific, one should provide a source to the claim <em>All names should according to the English grammar start with a capital letter</em>, but this is just an exam answer, and not a scientific report, so won't do that)</p>
<p>The student has successfully taken two true statements (<em>Peter is a name</em> and <em>All names should according to the English grammar start with a capital letter</em>) and used logic to deduce a new true statement (&quot;Peter is spelled with a capital P in the beginning&quot;).</p>
